import { ref, watch } from "vue";
import { useRoute } from "vue-router";
import PageHeader from '@/components/Header/pageHeader.vue';
import PageFooter from '@/components/Footer/pageFooter.vue';
import UserProjects from "@/components/User/userProjects.vue";
import UserTempProjects from "@/components/User/userTempProjects.vue";
import UserStars from "@/components/User/userStars.vue";
import UserSettings from "@/components/User/userSettings.vue";
import axios from "axios";
import eventSub from "@/utils/eventSub";
export default {
  name: "userPage",
  async setup() {
    const route = useRoute();
    const userInfo = ref(null);
    const currentTabName = ref(getTabName());
    //  tab 
    const isOwn = ref(false);
    // ?
    watch(route, () => {
      //  
      currentTabName.value = getTabName();
    });
    function getTabName() {
      //  tab?   tab 
      if (route.query.tab !== "projects" && route.query.tab !== "tempProjects" && route.query.tab !== "stars" && route.query.tab !== "settings") {
        return "projects";
      }
      return route.query.tab;
    }
    async function getUserInfo() {
      // 
      try {
        const rep = await axios.get(`/api/user/`, {
          params: {
            uid: route.params.uid
          }
        });
        if (rep.status != 200 || !rep.data || !rep.data.uid) {
          throw rep;
        }
        const userInfoRep = await axios.get(`/api/user/`);
        if (userInfoRep.status === 200 && userInfoRep.data && userInfoRep.data.uid && userInfoRep.data.uid === rep.data.uid) {
          // 
          isOwn.value = true;
        }
        userInfo.value = rep.data;
      } catch (e) {
        console.log("", e);
      }
    }
    await getUserInfo();
    eventSub.on("userPageUpdateInfo", async () => {
      //  
      await getUserInfo();
    });
    return {
      currentTabName,
      userInfo,
      isOwn
    };
  },
  components: {
    PageHeader,
    PageFooter,
    UserProjects,
    UserTempProjects,
    UserStars,
    UserSettings
  }
};