import axios from "axios";

import { Project } from "@/types";


export default async function(
    vueRouter: any,
    pid: string
): Promise<Project | void> {
    //  pid 
    //  

    const rep = await axios.get(`/api/projects/${pid}/`);

    if (rep.status !== 200 || !rep.data || (rep.data.code && rep.data.code !== 200)) {
        console.log(": ", rep);
        vueRouter.go(-1);
    }

    return rep.data;
}
