import axios from "axios";

import { TempProject } from "@/types";


export default async function(
    vueRouter: any,
    tpid: string
): Promise<TempProject | void> {
    //  tpid 
    //  

    const rep = await axios.get(`/api/projects/temporaryproject`, {
        params: {
            tpid
        }
    });

    if (rep.status !== 200 || !rep.data) {
        vueRouter.go(-1);
    }

    return rep.data;
}
